import { useState } from 'react';
function Counter() {
    let test = 1;
    const updateTest = (testval) => {
        console.log(testval);
        test = testval;
    }
    const [count, setCount] = useState(3);
    return (
        <div>
            <button onClick={() => updateTest(3)}>test</button>
            <label>{test}</label>
            <button onClick={() => setCount(count - 1)}>-</button>
            <label>{count}</label>
            <button onClick={() => setCount(count + 1)}>+</button>
        </div>)

}
export default Counter;