import React, { Component } from 'react';
import { useState } from 'react';
import Counter from '../counter';

function Count() {
    const [name, increment] = useState(0);

    // increment(){
    //     let q = 10;
    //     for (let i = 0; i < 10; i++) {

    //     }
    // }


    return (
        <div><button onClick={() => increment(name + 1)}>Increase{name} </button>

            <button onClick={() => increment(name - 1)}>Decrease{name} </button>

        </div>
    )
};

export default Count;