import React, { Component } from "react";
import "../../css/labourcard.css";
import imageepng from "../images/employe.png";
function Labourcard(props) {
  return (
    <div className="labourcard">
      <div className="image">
        <img src={imageepng} alt="employeicon"></img>
      </div>
      <p>{props.name}</p>
      <p>{props.id}</p>
      
      <input type="button" value="View" onClick={props.view}></input>
    </div>
  );
}

export default Labourcard;
