import React, { Component } from 'react';
import '../css/agechange.css'
class Agechange extends Component {

    state = { age: 25 };
    changeage() {
        if (this.state.age === 25) {
            this.setState({ age: 45 });
        } else {
            this.setState({ age: 25 });
        }

    }


    render() {


        return (

            <table className='Changeage'>
                <button onClick={() => this.changeage()}>click</button>

                <tr><th>Name</th>
                    <th>age</th>
                </tr>
                <tr>
                    <td>Akash</td>
                    <td>{this.state.age}</td>
                </tr>
            </table>)
    }
}
export default Agechange;