import { createSlice } from "@reduxjs/toolkit";

export const calcualtorSlice = createSlice({
  name: "calculator",
  initialState: {
    calTxt: "823",
  },
  reducers: {
    clear: (state) => {
      let splitedString = state.calTxt.split("");
      console.log(splitedString);

      if (splitedString.length >= 1) {
        splitedString.pop();
      }
      state.calTxt = splitedString.join("");
    },
    allClear: (state) => {
      state.calTxt = "";
    },
    addtext: (state, action) => {
      state.calTxt += action.payload;
    },
    solve: (state) => {
      state.calTxt = eval(state.calTxt);
    },
  },
});

export const { clear, allClear, addtext, solve } = calcualtorSlice.actions;

export default calcualtorSlice.reducer;
