import React, { Component } from "react";
// import "../../css/education.css";
import "../../css/education.css";
function Step1(props) {
  const changeStep = () => {
    props.setCurrentStep(2);
  };
  return (
    <div className="education">
      <div class="box">
        <h1>Step-1</h1>
        <form>
          <label for="name">Name:</label>
          <input type="text" placeholder="Enter Name"></input>
          <br />
          <label for="Date of Birth">DOB:</label>
          <input type="date"></input>
          <br />
          <label for="centre">State:</label>
          <select name="centre" id="centre">
            <option value="Select">Centre</option>
            <option value="Tamil Nadu">Tamil Nadu</option>
            <option value="Kerala">Kerala</option>
            <option value="Andrapradesh">Andra pradesh</option>
            <option value="Pondicherry">Pondicherry</option>
          </select>
          <br></br>

          <label>Gender:</label>
          <input type="radio" id="male" name="gender"></input>
          <label for="male" name="male">
            Male
          </label>
          <input type="radio" id="female" name="gender"></input>
          <label for="female" name="female">
            Female
          </label>
          <br></br>
          <label for="ug">UG</label>
          <input type="checkbox" id="ug"></input>
          <br></br>
          <span className="nextpage" onClick={() => changeStep()}>
            Next Step &gt;
          </span>
        </form>
      </div>
    </div>
  );
}

export default Step1;
