import React, { Component } from "react";
import "../../css/mark.css";
// import Step2 from "./step2";
import { Link } from "react-router-dom";
// import "../css/education.css";

const Mark = (props) => {
  // const  = (props) => {
  const changeStep = () => {
    props.setCurrentStep(3);
  };
  return (
    <div className="mark">
      <h1>Mark Details</h1>
      <form class="markform">
        <label for="marksheet">
          10<sup>th</sup>Marksheet
        </label>
        <input type="file" id="marksheet" name="marksheet"></input>
        <label for="marksheet">
          12<sup>th</sup>Marksheet
        </label>
        <input type="file" id="marksheet" name="marksheet"></input>
        <br />
        <input type="submit" value="Submit" onClick={()=> changeStep()}></input>
      </form>
      <div className="nav">
        <nav>Mark</nav>
        <nav>Step 2</nav>
        <nav>Attachment</nav>
      </div>
    </div>
  );
};

export default Mark;
