import React from "react";
import Step1 from "./education";
import Mark from "./mark";
import Step2 from "./step2";
import "../../css/parent.css";

const Parent = () => {
  const [currentStep, setCurrentStep] = React.useState(1);
  return (
    <div className="parent">
      <div>
        {currentStep === 1 && <Step1 setCurrentStep={setCurrentStep} />}
        {currentStep === 2 && <Step2 setCurrentStep={setCurrentStep} />}
        {currentStep === 3 && <Mark setCurrentStep={setCurrentStep} />}
      </div>
      <div>
        <span className={currentStep === 1 ? "active" : ""}>Step 1</span>
        <span className={currentStep === 2 ? "active" : ""}>Step 2</span>
        <span className={currentStep === 3 ? "active" : ""}>Step 3</span>
      </div>
    </div>
  );
};

export default Parent;
