import { useSelector } from "react-redux";
import imagepng from "./images/employe.png";
import Labourlist from "./Components/labourlist";
import { useNavigate } from "react-router-dom";
import "../css/labourdetails.css";

function Labourdetails() {
  const labour = useSelector((state) => state.labour.value);
  const navigate = useNavigate();
  const Back = () => {
    navigate("/labourlist");
  };

  return (
    <div>
      <div className="icon">
        <img src={imagepng} />
        <br />
        <span>Akash</span> <br />
        <span>101</span>
        <br></br>
        {/* <input type='button' value='click' name="clcik"></input> */}
        <input
          type="submit"
          value="Back"
          className="viewbutton"
          onClick={() => Back()}
        ></input>
      </div>

      <table class="labourtable">
        <tr>
          <th>Name:</th>
          <td>{labour.Name}</td>
        </tr>
        <tr>
          <th> Age :</th>
          <td>{labour.Age}</td>
        </tr>
        <tr>
          <th>Email:</th>
          <td>{labour.Email}</td>
        </tr>
        <tr>
          <th>Dateofjoining:</th>
          <td>{labour.Dateofjoining}</td>
        </tr>
        <tr>
          <th>Phonenumber:</th>
          <td> {labour.Phonenumber}</td>
        </tr>
        <tr>
          <th> Street:</th> <td>{labour.Street}</td>
        </tr>
        <tr>
          <th>City:</th> <td>{labour.City}</td>
        </tr>
        <tr>
          <th>Zip:</th> <td> {labour.Zip}</td>
        </tr>
        <tr>
          <th>Region:</th> <td>{labour.Region}</td>
        </tr>
        <th>Country:</th> <td>{labour.Country}</td>
        <tr>
          <th>Info:</th> <td>{labour.Info}</td>
        </tr>
        <tr>
          <th>EmployeeID:</th>
          <td>{labour.EmployeeID}</td>
        </tr>
      </table>
    </div>
  );
}

export default Labourdetails;
