import { useDispatch, useSelector } from "react-redux";
import React, { Component } from "react";
import "../css/calculator.css";
import {
  allClear,
  clear,
  addtext,
  solve,
} from "../store/reducer/calculator.reducer";

function Calculator() {
  const calsi = useSelector((state) => state.calculator.calTxt);
  const dispatch = useDispatch();

  return (
    <div>
      <div class="cals">
        <div id="input">{calsi}</div>

        <div class="Buttons">
          <hr />
          <div className="button" onClick={() => dispatch(allClear())}>
            {/* /* dispatch(increment())  */}c
          </div>
          <div className="button" onClick={() => dispatch(addtext("/"))}>
            /
          </div>
          <div className="button" onClick={() => dispatch(addtext("*"))}>
            *
          </div>
          <div className="button" onClick={() => dispatch(clear())}>
            &#8592;
          </div>
          <br />
          <div className="button" onClick={() => dispatch(addtext("7"))}>
            7
          </div>
          <div className="button" onClick={() => dispatch(addtext("8"))}>
            8
          </div>
          <div className="button" onClick={() => dispatch(addtext("9"))}>
            9
          </div>
          <div className="button" onClick={() => dispatch(addtext("-"))}>
            -
          </div>
          <br />
          <div className="button" onClick={() => dispatch(addtext("4"))}>
            4
          </div>
          <div className="button" onClick={() => dispatch(addtext("5"))}>
            5
          </div>
          <div className="button" onClick={() => dispatch(addtext("6"))}>
            6
          </div>
          <div className="button" onClick={() => dispatch(addtext("+"))}>
            +
          </div>
          <br />
          <div className="button" onClick={() => dispatch(addtext("1"))}>
            1
          </div>
          <div className="button" onClick={() => dispatch(addtext("2"))}>
            2
          </div>
          <div className="button" onClick={() => dispatch(addtext("3"))}>
            3
          </div>
          <div className="button" onClick={() => dispatch(addtext("."))}>.</div>
          <br />
          <div id="button" onClick={() => dispatch(addtext("2"))}>
            0
          </div>
          <div id="equal" class="button" onClick={() => dispatch(solve("2"))}>=</div>
          <hr />
        </div>
      </div>
    </div>
  );
}
export default Calculator;
