import React, { Component } from "react";
import "../../css/step2.css";

const Step2 = (props) => {
  const changeStep = () => {
    props.setCurrentStep(3);
  };
  return (
    <div className="parentbox">
      <div className="step2">
        <h1>Step-2</h1>
        <h2>Education Details</h2>
        <label for="schoolname" required>
          School Name :
        </label>
        <input type="text" id="schoolname" name="schoolname"></input>
        <br />
        <label for="centre">State :</label>
        <select name="centre" id="centre">
          <option value="Select">Centre</option>
          <option value="Tamil Nadu">Tamil Nadu</option>
          <option value="Kerala">Kerala</option>
          <option value="Andrapradesh">Andra pradesh</option>
          <option value="Pondicherry">Pondicherry</option>
        </select>
        <label>Accademic Year Start :</label>
        <input type="date" id="start"></input>
        <label>Accademic Year End :</label>
        <input type="date" id="end"></input>
        <span className="nextpage2" onClick={() => changeStep()}>
          Next Step &gt;<a href="" value="step2"></a>
        </span>

        {/* <div className="nav">
          <nav>Mark</nav>
          <nav>Step 2</nav>
          <nav>Attachment</nav>
        </div> */}
      </div>
    </div>
  );
};

export default Step2;
