import React, { Component } from "react";

class State extends Component {
  constructor() {
    super();
    this.state = {
      message: "Welcome",
    };
  }
  changestate() {
    this.setState({ message: "Haloa" });
  }
  render() {
    return (
      <div>
        <div>{this.state.message}</div>
        <button onClick={() => this.changestate()}>Click Here</button>
      </div>
    );
  }
}

export default State;
