import { useState } from "react";
import Labourcard from "./Components/labourcard";


function Sample() {

    const [message, setmessage] = useState('');

    // const [x, y];
    //['Hello Welcome', 'Have a good journey'] = useState('');

    return (<div>
        <button onClick={() => setmessage('Hello Welcome')}> Hi</button>
        <label>{message}</label>
        <button onClick={() => setmessage('Have a good journey')}> Bye</button>
        <Labourcard name="Akash" id="100" />
    </div>)
}
export default Sample;