import React, { Component } from "react";
import Table from "./table";

function Post() {
  const [postList, setpostList] = React.useState([]);
  React.useEffect(() => {
    fetch("https://jsonplaceholder.typicode.com/posts")
      .then((res) => res.json())
      .then((data) => {
        console.log(data);
        setpostList(data);
      });
  });
  return (
    <div>
      <p>
        {" "}
        {postList.length > 0 ? (
          <div>
            <Table data={postList} />
          </div>
        ) : (
          <div> Loading</div>
        )}
      </p>
    </div>
  );
}

export default Post;
