import React, { Component } from "react";
import { useEffect } from "react";
import "../css/table.css";

const Table = (props) => {
  const generatehead = () => {
    var heading = Object.keys(props.data[0]);
    return heading.map((each) => {
      return <th>{each} </th>;
    });
  };
  const generaterow = (data) => {
    return data.map((each) => {
      return <tr>{generatedata(each)}</tr>;
    });
  };
  const generatedata = (eachdata) => {
    var heading = Object.keys(props.data[0]);
    return heading.map((each) => {
      return <td>{eachdata[each]} </td>;
    });
  };
  const displaypage = () => {
    let start = (page - 1) * pagelimit;
    let end = start + pagelimit;
    return generaterow(props.data.slice(start, end));
  };
  const [page, setpage] = React.useState(1);
  const [pagelimit, setpagelimit] = React.useState(10);
  const [lastpage, setlastpage] = React.useState(1);
  const [reload, setreload] = React.useState(1);

  const generatebuttons = () => {
    let buttons = [];
    for (let i = 1; i <= lastpage; i++) {
      buttons.push(i);
    }
    return buttons.map((each) => (
      <button
        style={{ backgroundColor: each === page ? "red" : "grey" }}
        onClick={() => setpage(each)}
      >
        {each}
      </button>
    ));
  };

  useEffect(() => {
    setlastpage(Math.ceil(props.data.length / pagelimit));
  }, []);

  useEffect(() => {
    setreload(reload + 1);
  }, []);

  return (
    <div className="tableDynamic">
      Table{props.data.length}
      <div>
        <button class onClick={() => setpage(1)} disabled={page === 1}>
          &lt;&lt;
        </button>
        <button class onClick={() => setpage(page - 1)} disabled={page === 1}>
          &lt;
        </button>
        {/* <button class onClick={() => setpage(1)}>
          One
        </button> */}
        {/* <button onClick={() => setpage(2)}></button>
        <button onClick={() => setpage(3)}></button>
        <button onClick={() => setpage(4)}></button>
        <button onClick={() => setpage(5)}></button>
        <button onClick={() => setpage(6)}></button> */}
        <span>{reload % 2 === 0 ? generatebuttons() : generatebuttons()}</span>

        <button
          class
          onClick={() => setpage(page + 1)}
          disabled={page === lastpage}
        >
          &gt;
        </button>
        <button
          class
          onClick={() => setpage(lastpage)}
          disabled={page === lastpage}
        >
          &gt;&gt;
        </button>
      </div>
      <table className="headtable">
        <thead className="tablehead">
          <tr>{generatehead()} </tr>
        </thead>
        <tbody className="tablebody">{displaypage()}</tbody>
      </table>
    </div>
  );
};
export default Table;
